import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';

import Slider from 'react-slick';
import styled from 'styled-components';
import gstyles from '@vl/gstyles';
import TestimonialItem from '../TestimonialItem';
import CarouselNextButton from '../CarouselNextButton';
import CarouselPrevButton from '../CarouselPrevButton';

const CustomCarousel = styled.div`
  .slick-slider {
    .slick-dots {
      li {
        width: 8px;
        height: 8px;
        margin: 0 5px;
        button {
          background: ${gstyles.colors.brandb.lighter};
          border-radius: 50%;
          width: 8px;
          height: 8px;
        }
        &.slick-active {
          button {
            background: ${gstyles.colors.brandb500};
          }
        }
      }
    }
  }
`;

export const View8 = () => {
  return (
    <CustomCarousel>
      <DIV className="carouselContainer">
        <div className="py-8 sm:py-14 border-b-2 sm:border-b-0 rounded-none sm:rounded-xl bg-brandb-lightest">
          <h2 className="text-xl sm:text-4xl font-semibold text-center text-main">
            {ctx.get('sectionData.section.shortText')}
          </h2>
          <div className="sm:wrapper app-row px-0 py-4 sm:px-12">
            <Slider
              className="relative"
              {...ctx.get('carouselOptions')}
              {...{
                nextArrow: <CarouselNextButton onClick />,
                prevArrow: <CarouselPrevButton onClick />,
              }}
            >
              {ctx.get('sectionData.children', []).map((step, index) => {
                return <TestimonialItem key={index} itemData={step} />;
              })}
            </Slider>
          </div>
        </div>
      </DIV>
    </CustomCarousel>
  );
};
export default View8;
