import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';

import { useRenderProps } from '@vl/hooks/useRenderProps';

import AdvisorItemX from '@uz/unitz-components-web/AdvisorItemX';
import FlatList from '@uz/unitz-components-web/FlatList';
import { Row, Col, Button } from 'antd';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';

const renderItem = useRenderProps(
  ({ item, index }) => {
    return (
      <DIV forceCtx>
        {ctx.debug(() => {
          ctx.set('@item', item);
          ctx.set('@index', index);
        })}
        <div>
          <AdvisorItemX />
        </div>
      </DIV>
    );
  },
  ({ item, index }) => [index, _.get(item, 'id')]
);

const View8 = () => (
  <DIV className="component advisorListX 779BD3C8-CDF6-4067-A5A5-C1B320DFF758">
    <div className="sm:w-full py-6 sm:py-16">
      <h2 className="mb-6 sm:mb-10 text-xl sm:text-4xl font-semibold text-center text-main">
        {ctx.apply('i18n.t', 'Advisor.highlightAdvisorTitle')}
      </h2>
      <div className="block sm:hidden wrapper py-0 border-b"></div>
      <FlatList
        data={ctx.get('paginationModel.items', [])}
        renderItem={renderItem}
        keyExtractor={(item, index) => `${_.get(item, 'id')}_${index}`}
        horizontal
      />
      <div className="wrapper sm:px-0 py-0">
        <Row className="sm:justify-center mt-6 sm:mt-14" gutter={{ xs: 10, lg: 24 }}>
          {ctx.get('paginationModel.hasNextPage') && (
            <Col xs={12} lg={6}>
              <ResponsiveProps xs={{ size: 'small' }} lg={{ size: 'large' }}>
                {(resProps) => (
                  <Button
                    block
                    onClick={ctx.get('paginationModel.onLoadMore')}
                    loading={ctx.get('paginationModel.isLoading')}
                    {...resProps}
                  >
                    <span className="mb-0 text-base font-semibold text-brandb500">
                      {ctx.apply('i18n.t', 'Advisor.watchMore')}
                    </span>
                  </Button>
                )}
              </ResponsiveProps>
            </Col>
          )}
          <Col xs={12} lg={6}>
            <ResponsiveProps xs={{ size: 'small' }} lg={{ size: 'large' }}>
              {(resProps) => (
                <Button block onClick={ctx.get('advisorSearchForm.submitForm')} {...resProps}>
                  <span className="mb-0 text-base font-semibold text-brandb500">
                    {ctx.apply('i18n.t', 'Advisor.fitWithYou')}
                  </span>
                </Button>
              )}
            </ResponsiveProps>
          </Col>
        </Row>
      </div>
    </div>
  </DIV>
);

export default View8;
