import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import { Avatar } from '@uz/unitz-components-web/Avatar';

export const View8 = (props) => {
  const { itemData } = props;
  return (
    <DIV className="carouselItem">
      <div className="border-b sm:border-b-0 mx-5 py-3 sm:py-2 text-center grid grid-flow-col grid-cols-2 gap-4 sm:block">
        <div className="col-span-6 block sm:flex sm:items-center sm:justify-center mb-5">
          <Avatar
            size={{
              xs: 60,
              sm: 90,
              md: 100,
              lg: 64,
              xl: 60,
              xxl: 100,
            }}
            src={_.get(itemData, 'image.fixed.src', '')}
          />
        </div>
        <div>
          <h5 className="text-base text-main text-left mb-0 sm:mb-1 sm:text-center text-semibold">
            {_.get(itemData, 'shortText', '')}
          </h5>
          {ctx.apply('ctf.renderHTMLText', { name: itemData.name }, 'longText.longText', {
            className: 'text-sm font-normal mb-1 sm:mb-7 text-left sm:text-center text-sub',
            as: 'p',
          })}
          <p className="text-xs sm:text-base font-normal text-left sm:text-center text-main">
            {ctx.apply('ctf.renderRichText', { name: itemData.name }, 'richText', null)}
          </p>
        </div>
      </div>
    </DIV>
  );
};
export default View8;
