import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  component: {
    rules: [
      [
        'data',
        {
          data: {
            items: hook((ctx) => {
              const items = ctx.get('ctfCategories');
              return items || _.times(2, (index) => ({ id: index }));
            })
          }
        }
      ]
    ]
  }
});
export default bindData;
