import { bindings, hook } from '@vl/redata';
import _ from 'lodash';

const bindData = bindings({
  carouselContainer: {
    rules: [
      [
        'data',
        {
          data: {
            sectionData: hook((ctx) => {
              const section = ctx.apply('ctf.findSection', { name: 'TestimonialSection' });
              const children = _.get(section, 'sections', []).map((section) => {
                return ctx.apply('ctf.findEntry', section);
              });
              // console.log(section, children);
              return {
                section,
                children,
              };
            }),
            carouselOptions: hook(() => {
              return {
                dots: true,
                infinite: true,
                speed: 500,
                slidesToShow: 2,
                slidesToScroll: 2,
                responsive: [
                  {
                    breakpoint: 1024,
                    settings: {
                      slidesToShow: 1,
                      slidesToScroll: 1,
                      infinite: true,
                      dots: true,
                    },
                  },
                  {
                    breakpoint: 600,
                    settings: {
                      slidesToShow: 1,
                      slidesToScroll: 1,
                      initialSlide: 1,
                      swipeToSlide: true,
                      arrows: false,
                    },
                  },
                  {
                    breakpoint: 480,
                    settings: {
                      slidesToShow: 1,
                      slidesToScroll: 1,
                      swipeToSlide: true,
                      arrows: false,
                    },
                  },
                ],
              };
            }),
          },
        },
      ],
    ],
  },
});

export default bindData;
