import { hook, bindings } from '@vl/redata';

export default bindings({
  whyUs: {
    rules: [
      [
        'data',
        {
          data: {
            sectionData: hook((ctx) => {
              const section = ctx.apply('ctf.findSection', { name: 'WhyUsSection' });
              return {
                section
              };
            })
          }
        }
      ]
    ]
  }
});
