import React from 'react';
import DIV from '@vl/redata/DIV.macro';
import { ctx } from '@vl/redata';
import { Link } from '@uz/unitz-components-web/Link';
import { Row, Col, Button } from 'antd';
import styled from 'styled-components';
import gstyles from '@vl/gstyles';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';

const CustomText = styled.div`
  p > p {
    color: ${gstyles.colors.main};
    font-weight: 400;
  }
`;

const View8 = () => {
  return (
    <DIV className="questionList">
      <div className="wrapper app-row sm:mx-0 sm:w-full py-6 sm:py-16">
        <h2 className="mb-6 font-semibold text-main text-xl sm:text-4xl text-center">
          {ctx.get('sectionData.section.shortText')}
        </h2>
        <Row className="justify-center mb-6">
          <Col lg={18} sm={24} className="text-left sm:text-center">
            <CustomText>
              {ctx.apply('ctf.renderHTMLText', { name: ctx.get('sectionData.section.name') }, 'longText.longText', {
                className: 'text-sm sm:text-base',
                as: 'p',
              })}
            </CustomText>
          </Col>
        </Row>
        <div className="wrapper py-0 mb-10">
          <Row gutter={16} style={{ rowGap: '15px' }}>
            {ctx
              .get('sectionData.children', [])
              .slice(0, 6)
              .map((question) => {
                return (
                  <Col key={question.id} lg={12} sm={24}>
                    <Link className="list-item list-disc px-2" to={`/faqs#${question.slug}`}>
                      <p className="mb-0 font-normal text-sm sm:text-base text-brandb500">{question.shortText}</p>
                    </Link>
                  </Col>
                );
              })}
          </Row>
        </div>
        <Row className="justify-center" gutter={24}>
          <Col xs={24} lg={12}>
            <Link to="/faqs">
              <ResponsiveProps xs={{ size: 'small' }} lg={{ size: 'large' }}>
                {(resProps) => (
                  <Button {...resProps} onClick={ctx.get('form.onSubmit')} block>
                    <h5 className="mb-0 text-base font-semibold text-brandb500">
                      {ctx.apply('i18n.t', 'Question.more')}
                    </h5>
                  </Button>
                )}
              </ResponsiveProps>
            </Link>
          </Col>
        </Row>
      </div>
    </DIV>
  );
};

export default View8;
