import React from 'react';
import { bindings, hook } from '@vl/redata';
import useLoadingModel from '@vl/hooks/useLoadingModel';

import _ from 'lodash';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import useRoute from '@vl/hooks/useGbRoute';
import PaginationModel from '@uz/unitz-models/PaginationModel';
import AdvisorModel from '@uz/unitz-models/AdvisorModel';
import CategoryModel from '@uz/unitz-models/CategoryModel';
import usePromiseSource from '@vl/hooks/usePromiseSource';

const bindData = bindings({
  pageContainer: {
    rules: [
      [
        'data',
        {
          data: {
            loadingModel: hook(useLoadingModel(() => ({ fetchProfile: true }))),

            priceRangeOptions: hook((ctx) => {
              const options = {
                min: 100000,
                max: 5000000,
              };
              options.step = (options.max - options.min) / 20;
              options.defaultValue = [options.min + options.step, options.max - options.step];

              return options;
            }),

            advisorSearchForm: hook((ctx) => {
              const route = useRoute();
              const form = useFormik({
                initialValues: {
                  category: '',
                  service: 'video',
                  priceRange: ctx.get('priceRangeOptions.defaultValue'),
                },
                onSubmit: async (values, actions) => {
                  try {
                    const { priceRange } = form.values;
                    const [minPrice, maxPrice] = priceRange;
                    route.navigate(
                      route.to('/filter', {
                        ..._.omit(form.values, ['priceRange']),
                        min: minPrice,
                        max: maxPrice,
                      })
                    );
                  } catch (error) {
                    actions.setStatus({
                      error,
                    });
                  }
                },
                validationSchema: Yup.object().shape({}),
              });
              return form;
            }),

            categories: hook((ctx) => {
              const allContentfulCategory = ctx.apply('ctf.getNodes', 'allContentfulCategory.nodes', []);
              const catListFilterStr = _.map(allContentfulCategory, 'id')
                .map((item) => `"${item}"`)
                .join(',');

              const categories = usePromiseSource(
                () => async () => {
                  try {
                    const categories = await CategoryModel.find(
                      `where: { id: {_in: [${catListFilterStr}]}}, limit: 20`,
                      'id avatar_url display_name icon slug'
                    );
                    return categories.toObject();
                  } catch (err) {}
                },
                [],
                []
              );
              return categories;
            }),

            ctfCategories: hook((ctx) => {
              const allContentfulCategory = ctx.apply('ctf.getNodes', 'allContentfulCategory.nodes', []);
              return allContentfulCategory;
            }),

            advisorPagingModel: hook((ctx) => {
              const pagingModel = React.useMemo(() => {
                const pagingModel = PaginationModel.fromConfig({
                  Model: AdvisorModel,
                  dataQuery: `
                    search_advisor(args: {}, where: {is_active: {_eq: true}}, order_by: {profile: {updated_at: desc}}) {
                      id
                      profile {
                        id
                        display_name
                        avatar_url
                        ref_ctf_eid
                        advisor_id
                        created_at
                        intro_text
                        detail_html
                        slug
                        advisor_number
                        appraisal_type
                        advisor {
                          transactions_aggregate {
                            aggregate {
                              count
                            }
                          }
                        }
                      }
                      services {
                        price {
                          per_unit
                          per_amount
                          price_currency
                          price_amount
                          id
                          service {
                            id
                          }
                        }
                      }
                      services_aggregate {
                        aggregate {
                          count
                        }
                      }
                      review_agg {
                        avg_score
                      }
                    }
                  `,
                  aggQuery: `
                    search_advisor_aggregate(args: {}) {
                      aggregate {
                        count
                      }
                    }
                  `,
                  pageSize: 8,
                });
                return pagingModel;
              }, []);

              pagingModel.hookState();

              React.useEffect(() => {
                pagingModel.onLoadPageHandler(0);
              }, [pagingModel]);

              const advisorPagingModel = {
                items: pagingModel.useState('items'),
                page: pagingModel.useState('page'),
                isLoadMore: pagingModel.useState('isLoadMore'),
                isLoading: pagingModel.useState('isLoading'),
                hasPreviousPage: pagingModel.useState('hasPreviousPage'),
                hasNextPage: pagingModel.useState('hasNextPage'),
                hasLoadMore: pagingModel.useState('hasLoadMore'),
                onLoadMore: () => pagingModel.onLoadMore(),
                onRefresh: () => pagingModel.onRefresh(),
                onPreviousPage: () => pagingModel.onPreviousPageHandler(),
                onNextPage: () => pagingModel.onNextPageHandler(),
                onLoadPage: (page) => pagingModel.onLoadPageHandler(page),
              };
              return advisorPagingModel;
            }),
          },
        },
      ],
    ],
  },
  isLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
  notLoading: {
    rules: [
      [
        'display',
        {
          display: hook((ctx) => !ctx.apply('loadingModel.isLoading')),
        },
      ],
    ],
  },
});

export default bindData;
